  export const processFunction999 = (property) => {
    let allOn = true;
    let allOff = true;
    const results = [];
    for (let i = 0; i < property.length; i += 2) {
      const chunk = property.substring(i, i + 2);
      const buttonNumber = Math.floor(i / 2) + 1;
      const value = chunk === '01' ? 'ON' : 'OFF';
      if (value === 'ON') {
        results.push(buttonNumber);
      }
      if (value === 'OFF') {
        allOn = false;
      } else {
        allOff = false;
      }
    }
    if (allOn) {
      return [{ property: "CM", value: "ON" }];
    } else if (allOff) {
      return [{ property: "CM", value: "OFF" }];
    } else {
      return [{ property: "CM(ON)", value: results.join(',') }];
    }
  };
