export const ScDetailTable3 = ({ action, cm, bgm, minute }) => {
  return (
    <table align='center'>
      <tbody>
        <tr textalign='center'>
          <td colSpan={4}><b>BGM/CM</b></td>
        </tr>
        <tr>
          <th></th>
          <td colSpan={3}>{action}</td>
        </tr>
        <tr>
          <td width='22%'></td>
          <th width='26%'><br />(1)</th>
          <th width='26%'><br />(2)</th>
          <th width='26%'><br />(3)</th>
        </tr>
        <tr>
          <th>BGM</th>
          {bgm.slice(0, 3).map((value, index) => (
            <td key={index}>{value ? '' : ''}</td>
          ))}
        </tr>
        <tr>
          <th>CM</th>
          {cm.map((value, index) => (
            <td key={index}>{value ? '' : ''}</td>
          ))}
        </tr>
        <tr>
          <th></th>
          {minute.map((value, index) => (
            <td key={index}>{value ? '' : ''}</td>
          ))}
        </tr>
      </tbody>
    </table>
  )
};

